<template>
  <div class="fillcontain">
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="品牌名称" size="mini">
          <el-input v-model="params.brand_name" maxlength="100" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="品牌编号" size="mini">
          <el-input v-model="params.brand_id" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item label="助记码" size="mini">
          <el-input v-model="params.logogram" placeholder="请输入"></el-input>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" class="addBtn" size="mini" @click="dialogVisible = true" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" size="mini" @click="delFun" v-if="authority.b_del==1">删除</el-button>

        <el-dropdown placement="bottom-start" style="margin-left:10px;" v-if="authority.b_export==1">
          <span split-button style="cursor:pointer;color:#4e90e2">
            <el-button plain icon="iconfont icon-daochu10" size="mini">导出</el-button>
          </span>
          <el-dropdown-menu slot="dropdown" style="margin-top:10px;">
            <el-dropdown-item @click.native="exportFile(1)">导出Excel文件</el-dropdown-item>
            <el-dropdown-item @click.native="exportFile(2)">导出CSV逗号分隔</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" v-loading="loading1" max-height="600" resizable style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" :selectable="checkSelectable" width="50">
        </el-table-column>
        <el-table-column label="操作" fixed="right" width="54">
          <template slot-scope="scope">
            <el-button @click="updateRow(scope.row)" type="text" size="small">
              修改
            </el-button>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌名称" width="150"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_id" label="品牌编号" width="120"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="logogram" label="助记码" width="100"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="type_name" label="所属类型" width="130"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="auditor_is_disabled" :formatter="formatis_disabled" label="停用标志" width="80"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注"></el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>

    <!--新增和修改-->
    <el-dialog :close-on-click-modal="false" title="品牌管理" class="dialogForm" :visible.sync="dialogVisible" width="850px" @close="closeDialog">
      <div v-loading="loading">
        <el-form ref="goodsForm" :model="goodsForm" label-width="150px" size="mini">
          <el-form-item label="所属类型" prop="goods_type_id" :rules="[{ required: true, message: '请选择所属类型'}]" size="mini">
            <jybd-cascader v-if="dialogVisible"  ref="cascader" @cascaderChange="handleChange"></jybd-cascader>
          </el-form-item>
          <el-form-item label="品牌名称" :rules="[{ required: true, message: '品牌名称不能为空'}]" prop="brand_name" class="el-form-item_shortInput">
            <el-input v-model="goodsForm.brand_name" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item label="停用操作" class="el-form-item_shortInput">
            <el-radio-group v-model="goodsForm.is_disabled">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="备注" class="el-form-item_shortInput">
            <el-input type="text" v-model="goodsForm.remark" maxlength="300" placeholder="请输入"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm('goodsForm')">取 消</el-button>
        <el-button type="primary" @click="submitForm('goodsForm')" class="tx">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getData, exportFile } from "common/utils/public_function/commonUtils";
import { ThisScope } from "@/utils/commonCrud/CRUD.js";
export default {
  data() {
    return {
      authority: {},
      TableData: {
        tableData: [],
        per_page: [30, 50, 100, 500],
        total: 10,
      },
      loading: false,
      goodsForm: {
        is_disabled: 0,
      },
      selectedOptions: [],
      companyList: [],

      props: {
        value: "value",
        lable: "label",
        children: "child",
      },
      per_page: [30, 50, 100, 500],
      AccordingParams: {}, //参数传递值
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      AddOrExit: "Add",
      params: {
        // brand_name: "",
        // brand_id: "",
        // logogram: ""
      },
      dialogVisible: false,
      multipleSelection: [],
      currentPage: 1,
    };
  },
  methods: {
    // 清空组件 cascader 选中项
    clearCascader() {
      this.$refs.cascader.clear();
    },
    exportFile(scheduleType) {
      //导出
      if (!scheduleType) {
        this.$message({
          message: "请选择导出格式",
          type: "warning",
        });
        return false;
      }
      if (this.listShow == false) {
        this.$message({
          message: "请查询需要导出的数据",
          type: "warning",
        });
        return false;
      }
      let arg = {
        params: this.params,
        API: "exportBrandUrl",
      };
      exportFile(arg);
    },
    handleChange(val) {
      this.$set(this.goodsForm, "goods_type_id", val[2]);
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "params", {});

      this.$set(this, "AccordingParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getBrandList", //接口名
        vm: this, //传递的this
      });
      getData(this.AccordingParams);
    },
    formatis_disabled(row, column, cellValue) {
      return row.is_disabled == 1 ? "已停" : "未停";
    },
    checkSelectable(row) {
      return row.date !== "2016-05-01";
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    closeDialog() {
      // 清空弹框 所属类型选中项
      this.clearCascader();
      //关闭弹出框
      this.AddOrExit = "Add";
      this.selectedOptions = [];
      this.goodsForm = {
        is_del: 0,
        is_disabled: 0,
      };
    },
    updateRow(row) {
      this.AddOrExit = "Exit";
      this.loading = true; // 开启加载
      //修改
      var vm = this;
      this.$axios({
        method: "get",
        url: vm.$api.getGoodsBrandInfo,
        params: {
          brand_id: row.brand_id,
          id: row.id,
        },
      })
        .then((response) => {
          if (response.data.code == 200) {
            vm.goodsForm = {
              brand_name: response.data.data.brand_name,
              is_del: response.data.data.is_del,
              remark: response.data.data.remark,
              brand_id: row.brand_id,
              is_disabled: response.data.data.is_disabled,
              id: row.id,
              goods_type_id: row.goods_type_id,
            };
            vm.dialogVisible = true;
            vm.selectedOptions = [
              response.data.data.one_type_id,
              response.data.data.second_type_id,
              row.goods_type_id,
            ];
            vm.$nextTick(() => {
              vm.$refs.cascader.cascaderVue = vm.selectedOptions;
              console.log(vm.selectedOptions)
              vm.loading = false; // 开启加载
            });
          }
          vm.$nextTick(() => {
            vm.loading = false; // 关闭加载
          });
          //接口调取结束
        })
        .catch(() => {
          this.loading = false; // 开启加载
        });
    },
    handleSizeChange(val) {
      Object.assign(this.AccordingParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.AccordingParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.AccordingParams.params, {
        page: val,
      });
      getData(this.AccordingParams);
    },
    onSearch() {
      //查询
      Object.assign(this.AccordingParams.params, this.params, {
        page: 1,
      });
      getData(this.AccordingParams);
    },
    delFun() {
      new ThisScope({
        id: "brand_id",
        url: "delGoodsBrand",
        callbackFunc: this.onSearch,
        params: { id: this.multipleSelection.map((val) => val.id).join(",") },
        _this: this,
      }).send();
      // //删除
      // var deledata = {
      //   deleID: "brand_id",
      //   deleSelect: this.multipleSelection,
      //   deleApi: "delGoodsBrand",
      //   list: {
      //     UrlApi: "getBrandList",
      //     data: this.TableData,
      //   },
      // };
      // this.$common.deletes(deledata, "删除", this);
    },
    cancelForm(goodsForm) {
      //取消
      this.dialogVisible = false;
      this.$refs[goodsForm].resetFields();
    },
    submitForm(goodsForm) {
      //修改或新增提交
      var vm = this;
      this.$refs[goodsForm].validate((valid) => {
        if (valid) {
          this.dialogVisible = false;
          if (vm.goodsForm.brand_id == undefined) {
            // 新增
            var goodForm = {
              brand_name: vm.goodsForm.brand_name,
              goods_type_id: vm.goodsForm.goods_type_id,
              remark: vm.goodsForm.remark,
              is_disabled: vm.goodsForm.is_disabled,
            };
            var funcan = {
              eachData: goodForm,
              UrlApi: "addGoodsBrand",
              closeDialog: (this.dialogVisible = false),
              list: {
                UrlApi: "getBrandList",
                data: this.TableData,
                esData: this.AccordingParams.params,
                // esData: { page: this.goPage }
              },
            };
            this.$common.addAndupdateFun(funcan);
          } else {
            // 修改
            var goodForm = {
              brand_id: vm.goodsForm.brand_id,
              brand_name: vm.goodsForm.brand_name,
              // goods_type_id: vm.selectedOptions[vm.selectedOptions.length - 1], //修改可以传任何一级
              goods_type_id: vm.goodsForm.goods_type_id,
              remark: vm.goodsForm.remark,
              is_disabled: vm.goodsForm.is_disabled,
              id: vm.goodsForm.id,
            };
            var funcan = {
              eachData: goodForm,
              UrlApi: "editGoodsBrand",
              closeDialog: (this.dialogVisible = false),
              list: {
                UrlApi: "getBrandList",
                data: this.TableData,
                esData: this.AccordingParams.params,
                // esData: { page: this.goPage }
              },
            };
            this.$common.addAndupdateFun(funcan);
          }
        }
      });
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "AccordingParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getBrandList", //接口名
      vm: this, //传递的this
    });
    getData(this.AccordingParams);
  },
};
</script>
