//增删改查
// 弃审
import store from "@/store"; // vuex
// 判断一个函数是不是promise
function isPromise(val) {
  if (typeof (val) === 'function' && val().then) {
    return true
  }
}
export class ThisScope {
  constructor(Obj) {
    this._this = Obj._this;
    this.addr = [];
    this.indexSign = null;
    this.URL = Obj.url;
    this.id = Obj.id;
    this.errId = Obj.errId ? Obj.errId : Obj.id // 用于错误行的显示
    this.title = Obj.title ? Obj.title : '审核'
    this.allTitle = Obj.allTitle
    this.params = Obj.params// 额外参数
    this.before = Obj.before // 再请求之前的校验，可选值 promise  function
    this.checkbox = null;
    this.Data = null;
    this.showConfirm = Obj.showConfirm
    this.callbackFunc = Obj.callbackFunc
  }
  verify() {
    if (this._this.multipleSelection.length < 1) {
      this._this.$message({
        message: `请选择需要${this.title}的信息`,
        type: "warning"
      });
      return false;
    } else {
      return true
    }
  }

  confirm() {
    var title = '确定要“' + this.title + '”此条信息?'
    if (this.allTitle) {
      title = this.allTitle
    }
    return this._this.$confirm(title, '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
  }

  // 发送请求
  sendAjax(rebateArr, resolve, reject) {
    var ajaxFunc;
    var params = Object.assign({ [this.id]: rebateArr }, this.params)
    if (this._this.$axMethod[this.URL]) {
      ajaxFunc = this._this.$axMethod[this.URL](params)
    } else if (this._this.$api[this.URL]) {
      ajaxFunc = this._this.$axios({
        url: this._this.$api[this.URL],
        method: 'post',
        data: params
      })
    }
    ajaxFunc.then(res => {
      this.checkbox = this._this.multipleSelection; //选择
      this.callbackFunc && this.callbackFunc(res.data)
      if (res.data.code == 200) {
        resolve(res)
        this.Data = res;
      } else {
        reject(res)
      }
    }).catch(err => {
      reject(err)
    });
  }
  logic() {
    if (!this.verify()) {
      return false
    }
    for (var i = 0; i < this._this.multipleSelection.length; i++) {
      this.addr.push(this._this.multipleSelection[i][this.id]);
    }
    var rebateArr = this.addr.toString();

    return new Promise((resolve, reject) => {
      if (this.showConfirm == true) {
        this.confirm().then(() => {
          if (isPromise(this.before)) {// promise
            this.before(rebateArr).then(() => {
              return this.sendAjax(rebateArr, resolve, reject)
            })
          } else if ((this.before && this.before()) || !this.before) {// 传了before函数就是校验是否为true 不传直接可以进来
            return this.sendAjax(rebateArr, resolve, reject)
          }
        })
      } else {
        if (isPromise(this.before)) {// promise
          this.before(rebateArr).then(() => {
            return this.sendAjax(rebateArr, resolve, reject)
          })
        } else if ((this.before && this.before()) || !this.before) {// 传了before函数就是校验是否为true 不传直接可以进来
          return this.sendAjax(rebateArr, resolve, reject)
        }
      }
    })

  }
  send() {
    this.logic().then(({ data }) => {
      if (data.code === 200) {
        this._this.$message.success(data.msg ? data.msg : data.data)
        this._this.onSearch && this._this.onSearch();
      }
    })
  }
  async callback() {
    try {
      await this.logic()
      if (this.Data && this.Data.data && this.Data.data.data.length) {
        let errData = this.Data.data.data; //报错
        let checkArry = [];
        errData.forEach((item) => {
          checkArry.push(item.serial);
        });
        let activeColor = this.checkbox.filter(
          val => checkArry.includes(val[this.errId]) == true
        );
        this._this.indexSign.arr = activeColor
        this._this.indexSign.str = this.errId
        store.commit('setErrorObj', {
          msg: this.Data.data.msg,
          table: this.Data.data.data,
          state: true
        })
      } else {
        let msg = this.Data.data.msg ? this.Data.data.msg : this.Data.data.data;
        this._this.indexSign.arr = []
        this._this.$message({
          message: msg,
          type: "success"
        });
        this._this.multipleSelection = [];
        this._this.onSearch && this._this.onSearch();
      }
    } catch (error) {
      console.log()
    }
  }
}
// 详情的操作
export class seeEdit {
  constructor({ _this, url, list = [], params = {}, before }) {
    this._this = _this
    this.url = url // 操作的url
    this.list = list // 商品
    this.params = params // 参数
    this.before = before // 在请求之前做处理
  }
  sendAjax(resolve, reject) {
    var ajaxFunc;
    if (this._this.$axMethod[this.url]) {
      ajaxFunc = this._this.$axMethod[this.url](this.params)
    } else if (this._this.$api[this.url]) {
      ajaxFunc = this._this.$axios({
        url: this._this.$api[this.url],
        method: 'post',
        data: this.params
      })
    }
    ajaxFunc.then(res => {
      if (res.data.code == 200) {
        resolve(res)
      } else {
        reject(res)
      }
    }).catch(err => {
      reject(err)
    });
  }
  edit() {
    return new Promise((resolve, reject) => {
      if (isPromise(this.before)) {// promise
        this.before().then(() => {
          return this.sendAjax(resolve, reject)
        })
      } else if ((this.before && this.before()) || !this.before) {// 传了before函数就是校验是否为true 不传直接可以进来
        return this.sendAjax(resolve, reject)
      }
    })
  }
  callback() {
    return new Promise((resolve, reject) => {
      this.edit().then((res) => {
        if (res.data && res.data.data && res.data.data.length) {
          store.commit('setErrorObj', {
            msg: res.data.msg,
            table: res.data.data,
            state: true
          })
          var result = []
          res.data.data.forEach((item) => {
            result.push(item.serial);
          });
          this._this.indexSign = result
          reject(res)
        } else {
          this._this.$message({
            message: res.data.msg ? res.data.msg : res.data.data,
            type: "success"
          });
          resolve(res)
        }
      })
    })
  }
}